@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

html {
  height: 100%;
}

body {
  margin: 0;
  display: flex;
  flex-direction: column;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  color: #000000;
  text-align: center;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
}

.main-header {
  display: flex;
  justify-content: center;
  background-color: #f1f1f1;
  position: relative;
  max-width: 1020px;
  margin: 0 auto;
  padding-left: 10px;
}

.main-header__link {
  display: block;
  color: #2c4229;
  text-decoration: none;
  padding-top: 20px;
  padding-bottom: 20px;
}
.main-header__link:hover {
  color: #61a897;
}
.main-header__link:active {
  color: rgb(235, 235, 203);
}

.main-nav {
  position: absolute;
}

.main-header__nav {
  position: absolute;
  top: 64px;
}
.main-header__nav--close {
  display: none;
}
.main-header__nav--opened {
  display: block;
}

.main-header__button {
  padding: 0;
  position: absolute;
  right: 23px;
  top: 22px;
  border: none;
  width: 30px;
  height: 21px;
  cursor: pointer;
}

.main-header__nav--close + .main-header__button::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  background-image: url("../img/icons/button-close.svg");
  background-repeat: no-repeat;
}

@media (min-width: 768px) {
  .main-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .main-header__link {
    font-size: 19px;
    padding-top: 30px;
    padding-left: 50px;
    padding-bottom: 30px;
  }
  .main-header__button {
    display: none;
  }
  .main-header__nav {
    position: static;
  }
  .main-header__nav--close {
    display: flex;
  }
}
.main-nav {
  background-color: #ffffff;
  width: 100%;
}

.main-nav__list {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  list-style-type: none;
  text-align: right;
  text-transform: uppercase;
  width: 100%;
}

.main-nav__item {
  padding-left: 80px;
  box-shadow: inset 0 0 1px #61a897;
  position: relative;
}
.main-nav__item::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 22px;
  top: 12px;
  left: 35px;
  background-image: url("../img/icons/array.svg");
  background-repeat: no-repeat;
}

.main-nav__link {
  font-size: 12px;
  display: block;
  color: #61a897;
  text-decoration: none;
  padding-top: 10px;
  padding-bottom: 10px;
  text-align: left;
  cursor: pointer;
}
.main-nav__link:hover {
  color: #2c4229;
}
.main-nav__link:active {
  color: rgb(235, 235, 203);
}

@media (min-width: 768px) {
  .main-nav {
    width: auto;
  }
  .main-nav__list {
    flex-direction: row;
    background-color: #f1f1f1;
    padding-right: 50px;
  }
  .main-nav__item {
    padding-left: 0;
    margin-right: 20px;
    box-shadow: none;
    background-color: #f1f1f1;
  }
  .main-nav__item::before {
    display: none;
  }
  .main-nav__item .main-nav__link {
    font-size: 14px;
    padding-top: 30px;
    padding-bottom: 30px;
  }
}
@media (min-width: 1150px) {
  .main-nav__item {
    margin-right: 30px;
  }
}
.main-container__slider {
  margin: 0 auto;
}

/* General styles */
.sim-slider {
  position: relative;
  width: 260px;
  height: 146px;
  margin: 0 auto;
  padding-top: 30px;
  margin-bottom: 30px;
}

.sim-slider-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  position: relative;
}

.sim-slider-element {
  width: 100%;
  transition: opacity 1s ease-in;
  opacity: 0;
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  display: block;
}

.sim-slider-image {
  width: 100%;
}

/* Navigation item styles */
div.sim-slider-arrow-left,
div.sim-slider-arrow-right {
  width: 22px;
  height: 40px;
  position: absolute;
  cursor: pointer;
  opacity: 0.6;
  z-index: 4;
}

div.sim-slider-arrow-left {
  left: 10px;
  top: 40%;
  display: block;
  background: url("sim-arrow-left.png") no-repeat;
}

div.sim-slider-arrow-right {
  right: 10px;
  top: 40%;
  display: block;
  background: url("sim-arrow-right.png") no-repeat;
}

div.sim-slider-arrow-left:hover {
  opacity: 1;
}

div.sim-slider-arrow-right:hover {
  opacity: 1;
}

div.sim-slider-dots {
  width: 100%;
  height: auto;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 3;
  text-align: center;
}

span.sim-dot {
  width: 10px;
  height: 10px;
  margin: 5px 7px;
  padding: 0;
  display: inline-block;
  background-color: #ffffff;
  border-radius: 5px;
  cursor: pointer;
}

@media (min-width: 768px) {
  .sim-slider {
    width: 460px;
    height: 258px;
    margin-bottom: 0;
  }
}
@media (min-width: 1150px) {
  .sim-slider {
    width: 660px;
    height: 371px;
  }
}
.advantages__title {
  font-size: 16px;
  width: 239px;
  margin: 0 auto;
  margin-bottom: 20px;
}

.advantages__list {
  font-size: 14px;
  font-weight: 400;
  color: #61a897;
  text-align: left;
  list-style-type: none;
  padding: 0;
  width: 150px;
  margin: 0 auto;
  padding-left: 60px;
}

.advantages__item {
  margin-bottom: 10px;
  position: relative;
}
.advantages__item--eco::before {
  content: "";
  position: absolute;
  top: 7px;
  left: -60px;
  background-image: url("../img/advantages/eco.svg");
  width: 32px;
  height: 32px;
}
.advantages__item--free::before {
  content: "";
  position: absolute;
  top: 7px;
  left: -60px;
  background-image: url("../img/advantages/free.svg");
  width: 32px;
  height: 32px;
}
.advantages__item--delivery::before {
  content: "";
  position: absolute;
  top: 7px;
  left: -60px;
  background-image: url("../img/advantages/delivery.svg");
  width: 32px;
  height: 32px;
}
.advantages__item--support::before {
  content: "";
  position: absolute;
  top: 7px;
  left: -60px;
  background-image: url("../img/advantages/support.svg");
  width: 32px;
  height: 32px;
}
.advantages__item--tech::before {
  content: "";
  position: absolute;
  top: 7px;
  left: -60px;
  background-image: url("../img/advantages/tech.svg");
  width: 32px;
  height: 32px;
}
.advantages__item--garanty::before {
  content: "";
  position: absolute;
  top: 7px;
  left: -60px;
  background-image: url("../img/advantages/garanty.svg");
  width: 32px;
  height: 32px;
}

@media (min-width: 768px) {
  .advantages__title {
    font-size: 22px;
    width: 370px;
    margin: 0 auto;
    margin-bottom: 40px;
    padding-top: 40px;
  }
  .advantages__list {
    font-size: 16px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 80px 80px 80px;
    width: 600px;
    justify-content: space-between;
    margin-bottom: 20px;
    padding-left: 30px;
  }
  .advantages__item {
    max-width: 160px;
    padding-left: 100px;
  }
  .advantages__item::before {
    left: 30px;
  }
}
@media (min-width: 1150px) {
  .advantages__title {
    font-size: 28px;
    line-height: 1.3;
    width: 600px;
    margin: 0 auto;
    margin-bottom: 40px;
    padding-top: 40px;
  }
  .advantages__list {
    font-size: 16px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 80px 80px;
    width: 900px;
    justify-content: space-between;
    margin-bottom: 20px;
    padding-left: 30px;
  }
}
.brands {
  background-color: #f1f1f1;
}

.brands__list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 30px;
  margin: 0;
}

.brands__item {
  background-color: #f1f1f1;
  margin-bottom: 5px;
  border-radius: 40px;
  width: 230px;
  border: 2px solid #61a897;
}

.brands__link {
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  color: #2c4229;
  padding: 10px 40px;
  cursor: pointer;
}

@media (min-width: 768px) {
  .brands__list {
    flex-direction: row;
    justify-content: space-between;
    max-width: 820px;
    margin-left: auto;
    margin-right: auto;
    padding: 40px 30px;
  }
  .brands__item {
    border-radius: 5px;
  }
  .brands__item:hover {
    transform: scale(1.05);
    background-color: #61a897;
  }
  .brands__link {
    padding: 50px 40px;
  }
  .brands__link:hover {
    color: #f1f1f1;
  }
  .brands__link:active {
    color: rgb(235, 235, 203);
  }
}
.contacts {
  display: flex;
  flex-direction: column;
  padding: 10px 30px;
  margin: 0 auto;
  position: relative;
}
.contacts::before {
  content: "";
  position: absolute;
  background-image: url("../img/icons/map.svg");
  width: 70px;
  height: 70px;
  background-repeat: no-repeat;
  top: 80px;
  right: 200px;
}

.contacts__adress {
  text-align: right;
  width: 150px;
  margin: 0 0 10px auto;
}

.contacts__text {
  text-align: right;
  font-size: 14px;
  width: 100px;
  margin: 0 0 0 auto;
}

.contacts__link {
  color: #61a897;
  margin-left: auto;
  text-decoration: none;
  cursor: pointer;
}
.contacts__link:hover {
  color: rgb(235, 235, 203);
}
.contacts__link:active {
  color: rgb(235, 235, 203);
}
.contacts__link--number {
  margin-bottom: 10px;
}

.contacts__map {
  min-width: 260px;
  height: 300px;
  cursor: pointer;
}

@media (min-width: 768px) {
  .contacts {
    max-width: 660px;
    padding: 30px 30px;
  }
  .contacts::before {
    right: 500px;
  }
  .contacts__adress {
    font-size: 20px;
    width: 300px;
  }
  .contacts__text {
    font-size: 18px;
    width: 130px;
  }
  .contacts__map {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;
    width: 660px;
    height: 350px;
  }
}
@media (min-width: 1150px) {
  .contacts {
    max-width: 825px;
  }
  .contacts::before {
    right: 600px;
  }
  .contacts__map {
    width: 825px;
    height: 400px;
  }
  .contacts__link--number {
    margin-bottom: 30px;
  }
}
.footer {
  background-color: #f1f1f1;
  display: flex;
  justify-content: center;
}

.social__list {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
  padding: 0;
  width: 100px;
}

.social__link {
  display: block;
  width: 25px;
  height: 25px;
  cursor: pointer;
  fill: #61a897;
}
.social__link:hover {
  transform: scale(1.2);
  fill: #2c4229;
}
.social__link:active {
  fill: rgb(235, 235, 203);
}

@media (min-width: 768px) {
  .social__list {
    width: 200px;
    padding-top: 20px;
    margin-bottom: 30px;
  }
}
.page__header {
  width: 100%;
  background-color: #f1f1f1;
}