.social__list {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
  padding: 0;
  width: 100px;
}

.social__link {
  display: block;
  width: 25px;
  height: 25px;
  cursor: pointer;
  fill: #61a897;

  &:hover {
    transform: scale(1.2);
    fill: $logo;
  }

  &:active {
    fill: $active;
  }
}

@media (min-width: $tablet-width) {
  .social__list {
    width: 200px;
    padding-top: 20px;
    margin-bottom: 30px;
  }
}
